const allMenus = (state, getters) => {
    // 添加根菜单
    return [{
        menu_name: '全部菜单',
        menu_id: 0,
        route_id: 0,
        permission_id: "0",
        permission_code: "",
        permission_remark: "根菜单",
        resource_type_id: 2,
        resource_type_code: "",
        resource_type_name: "根菜单",
        children: getters.menus_tree
    }];
};
// 树形结构转换函数
const parseToTree = function (origin, list) {
    return list.map((parent) => {
        let children = origin.filter((child) => child.parent_id === parent.permission_id);
        if (children.length) {
            return { ...parent, children: parseToTree(origin, children) }
        } else {
            return { ...parent }
        }
    });
}
// 菜单--树形结构
const menus_tree = (state) => {
    let menu_1st = state.menus.filter((menu) => menu.parent_id === 0);
    //深拷贝，防止修改原始数组
    return parseToTree(state.menus, menu_1st);
}

// 根据路由选中状态、菜单选中状态、生成新的树形结构菜单
const menu_from_checked_route = (state, getters) => {
    return (route_checked_nodes) => {
        let menus = state.menus.map((item) => {
            // 深拷贝
            let node = JSON.parse(JSON.stringify(item));
            // 节点默认禁用状态
            // node.disabled = true;
            // 路由节点
            if (node.resource_type_id === 2) {
                // 查询是否选中路由，
                let isExist = route_checked_nodes.some((route) => route.route_id === node.route_id);
                // 开启/禁用节点
                node.disabled = !isExist;
            }
            // 操作按钮节点
            if (node.resource_type_id === 3) {
                // 查找操作按钮的父级
                let parent_menu = state.menus.find((menu) => menu.permission_id === node.parent_id);
                // 查询其父级是否选中
                let isChecked = route_checked_nodes.some((route) => route.route_id === parent_menu.route_id);
                // 根据父级是否选中，判断其状态
                node.disabled = !isChecked;
            }
            return node;
        });
        // 树形结构数组--递归
        let menu_1st = menus.filter((menu) => menu.parent_id === 0);
        return parseToTree(menus, menu_1st);
    }
}

export default {
    allMenus,
    menus_tree,
    menu_from_checked_route,
}