import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import '@/plugins/element';
import '@/plugins/axios';

// 环境变量
Vue.prototype.$server = process.env.VUE_APP_SERVER;

Vue.config.productionTip = false

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
