<template>
  <el-menu router :collapse="isCollapse" unique-opened class="side-bar" background-color="#545c64" text-color="#fff"
           active-text-color="#ffd04b">
    <template v-for="(item,index) in menu">
      <el-submenu v-if="item?.children" :index="item.route_full_path">
        <template slot="title">
          <i :class="`el-icon-${item.icon_name}`"></i>
          <span slot="title">{{ item.menu_name }}</span>
        </template>
        <el-menu-item v-for="element in item.children" :key="element.menu_id" :index="element.route_full_path">
          <i :class="`el-icon-${element.icon_name}`"></i>
          {{ element.menu_name }}
        </el-menu-item>
      </el-submenu>

      <el-menu-item v-else :index="item.route_full_path">
        <i :class="`el-icon-${item.icon_name}`"></i>
        <span slot="title">{{ item.menu_name }}</span>
      </el-menu-item>

    </template>
  </el-menu>
</template>

<script>
import { mapState } from 'vuex'
import { Role } from "@/api/";

export default {
  data() {
    return {
      menu: [],
    }
  },
  created() {
    this.load_menu_list();
  },
  computed: {
    ...mapState("SideBar", ['isCollapse'])
  },
  methods: {
    // 获取当前角色的菜单权限
    async load_menu_list() {
      // 提取角色id
      let role_id = sessionStorage.role_id;
      let { status, data } = await Role.menu({ role_id, type: 'tree', resource_type: 2 });
      if (status) {
        this.menu = data;
      }
    }
  }
}
</script>

<style scoped lang="less">
.side-bar:not(.el-menu--collapse) {
  width: 200px;
}
</style>
