import { Role } from '@/api/';

// 加载所有操作按钮
const load_role_list = async ({ commit }, query) => {
    let { status, data } = await Role.list(query);
    if (status) {
        // 储存到state
        commit('STORE_ROLES', data);
    }
    return { status, data };
};

//添加操作按钮
const create_role = async ({ commit }, roleData) => {
    let { status, msg, data } = await Role.insert(roleData);
    if (status) {
        // 储存到state
        commit('APPEND_ROLE', { ...data, ...roleData });
    }
    return { status, msg, data };
};

//编辑操作按钮
const edit_role = async ({ commit }, roleData) => {
    let { status, msg } = await Role.edit(roleData.role_id, roleData);
    if (status) {
        // 储存到state
        commit('UPDATE_ROLE', roleData);
    }
    return { status, msg };
};

//删除操作按钮
const remove_role = async ({ commit }, roleData) => {
    let { role_id, permission_id } = roleData;
    let { status, msg } = await Role.remove(role_id, { permission_id });
    if (status) {
        // 储存到state
        commit('DELETE_ROLE', role_id);
    }
    return { status, msg };
};

export default {
    load_role_list,
    create_role,
    edit_role,
    remove_role,
}
