import { Admin } from '@/api/index';

const load_profile = async ({ commit, state }, formData) => {
    let { status, data } = await Admin.info(formData);
    if (status) {
        // 储存到state
        commit('UPDATE_PROFILE', data);
    }
    return { status, data };
};

const edit_profile = async ({ commit, state }, formData) => {
    let { status, msg } = await Admin.account(formData);
    if (status) {
        // 储存到state
        commit('UPDATE_PROFILE', formData);
    }
    return { status, msg };
};
export default {
    load_profile,
    edit_profile,
}
