// 储存菜单数据
const STORE_MENUS = (state, listData) => {
    state.menus = listData;
};

// 添加菜单
const APPEND_MENU = (state, menuData) => {
    state.menus.push(menuData);
}

// 编辑菜单
const UPDATE_MENU = (state, menuData) => {
    let menu = state.menus.find((menu) => menu.menu_id === menuData.menu_id);
    // Object.assign 该方法使用源对象的[[Get]]和目标对象的[[Set]]，所以它会调用相关 getter 和 setter。
    Object.assign(menu, menuData);
};

// 删除菜单
const DELETE_MENU = (state, menu_id) => {
    let index = state.menus.findIndex((menu) => menu.menu_id === menu_id);
    state.menus.splice(index, 1);
}

// 设置菜单图标
const SET_MENU_ICON = (state, payload) => {
    let menu = state.menus.find((menu) => menu.menu_id === payload.menu_id);
    // 合并新数据
    Object.assign(menu, payload);
}

// 菜单--编辑操作按钮
const UPDATE_MENU_OPERATION = (state, operationData) => {
    let operation = state.menus.find((menu) => menu.permission_id === operationData.permission_id);
    //Object.assign 该方法使用源对象的[[Get]]和目标对象的[[Set]]，所以它会调用相关 getter 和 setter。
    Object.assign(operation, operationData);
};

// 菜单--删除操作按钮
const DELETE_MENU_OPERATION = (state, permission_id) => {
    let index = state.menus.findIndex((menu) => menu.permission_id === permission_id);
    state.menus.splice(index, 1);
}

export default {
    STORE_MENUS,
    APPEND_MENU,
    UPDATE_MENU,
    DELETE_MENU,
    SET_MENU_ICON,
    UPDATE_MENU_OPERATION,
    DELETE_MENU_OPERATION,
}