import { Menu } from '@/api/';

// 加载所有菜单
const load_menu_list = async ({ commit }, query) => {
    let { status, data } = await Menu.loadAll(query);
    if (status) {
        // 储存到state
        commit('STORE_MENUS', data);
    }
    return { status, data };
};

//添加菜单
const create_menu = async ({ commit }, menuData) => {
    let { status, msg, data } = await Menu.insert(menuData);
    if (status) {
        // 储存到state
        commit('APPEND_MENU', { ...data, ...menuData });
    }
    return { status, msg, data };
};

//编辑菜单
const edit_menu = async ({ commit }, menuData) => {
    let { menu_id } = menuData;
    let { status, msg } = await Menu.update(menu_id, menuData);
    if (status) {
        // 储存到state
        commit('UPDATE_MENU', menuData);
    }
    return { status, msg };
};

//删除菜单
const remove_menu = async ({ commit }, menuData) => {
    let { menu_id, permission_id } = menuData;
    let { status, msg } = await Menu.remove(menu_id, { permission_id });
    if (status) {
        // 储存到state
        commit('DELETE_MENU', menu_id);
    }
    return { status, msg };
};

//设置菜单图标
const set_menu_icon = async ({ commit }, payload) => {
    let { menu_id, icon_id } = payload;
    let { status, msg } = await Menu.set_icon(menu_id, { icon_id });
    if (status) {
        // 储存到state
        commit('SET_MENU_ICON', payload);
    }
    return { status, msg };
};

// 菜单页面--添加操作按钮
const create_menu_operation = async ({ commit }, operationData) => {
    let { status, msg, data } = await Menu.create_operation(operationData);
    if (status) {
        // 储存到state
        commit('APPEND_MENU', { ...data, ...operationData });
    }
    return { status, msg, data };
};

// 菜单页面--编辑操作按钮
const edit_menu_operation = async ({ commit }, operationData) => {
    let { permission_id } = operationData;
    let { status, msg } = await Menu.edit_operation(permission_id, operationData);
    if (status) {
        // 储存到state
        commit('UPDATE_MENU_OPERATION', operationData);
    }
    return { status, msg };
};

//菜单页面--删除操作按钮
const remove_menu_operation = async ({ commit }, operationData) => {
    let { permission_id } = operationData;
    let { status, msg } = await Menu.remove_operation(permission_id);
    if (status) {
        // 储存到state
        commit('DELETE_MENU_OPERATION', permission_id);
    }
    return { status, msg };
};

export default {
    load_menu_list,
    create_menu,
    edit_menu,
    remove_menu,
    set_menu_icon,
    create_menu_operation,
    edit_menu_operation,
    remove_menu_operation,
}
