<template>
  <el-container>
    <el-header>
      <nav-bar></nav-bar>
    </el-header>
    <el-container>
      <el-aside width="auto">
        <side-bar></side-bar>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import { Role } from '@/api/';
import SideBar from '@/components/SideBar.vue';
import NavBar from '@/components/NavBar.vue';

export default {
  components: {
    SideBar,
    NavBar
  },
  created() {
    // 操作按钮权限
    this.load_operation_list();
    // 路由权限
    this.load_route_list();
  },
  methods: {
    // TODO 获取当前角色的操作按钮权限
    async load_operation_list() {
      let role_id = sessionStorage.role_id;
      let { status, data } = await Role.menu({ role_id, resource_type: 3 });
    },
    //  获取当前角色的路由权限，注册动态路由
    async load_route_list() {
      let role_id = sessionStorage.role_id;
      let { status, data } = await Role.route({ role_id });
      let routes = data.map((route) => {
        let { route_path, route_name, component_name, component_path } = route;
        return { path: route_path, name: route_name, component: () => import(component_path) }
      });
      console.log(routes)
    },
  }
}
</script>

<style lang="less" scoped>
.el-header {
  background-color: #31404e;
  color: white;
  padding: 0px;
}

.el-aside {
  background-color: #545c64;
  min-height: calc(100vh - 60px);
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 6px;
    background: white;
  }

  &::-webkit-scrollbar-thumb {
    background: transparent;
    border-radius: 5px;
  }

  &:hover::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.3);
  }

  &:hover::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.1);
  }
}

.el-main {
  background-color: white;
  color: #333;
}
</style>
