// 储存操作按钮数据
const STORE_ROLES = (state, listData) => {
    state.roles = listData;
};

//添加操作按钮
const APPEND_ROLE = (state, roleData) => {
    state.roles.push(roleData);
}

//编辑操作按钮
const UPDATE_ROLE = (state, roleData) => {
    let role = state.roles.find((role) => role.role_id === roleData.role_id);
    //Object.assign 该方法使用源对象的[[Get]]和目标对象的[[Set]]，所以它会调用相关 getter 和 setter。
    Object.assign(role, roleData);
};

//删除操作按钮
const DELETE_ROLE = (state, role_id) => {
    let index = state.roles.findIndex((role) => role.role_id === role_id);
    state.roles.splice(index, 1);
}

export default {
    STORE_ROLES,
    APPEND_ROLE,
    UPDATE_ROLE,
    DELETE_ROLE,
}